.xf{
    top: 0%;
    right: 50%;
    bottom: 0%;
    left: 50%;
    position: fixed;
    font-family: 'Beth Ellen', cursive;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 20vmin;
    color: #b82bbd;
  }
  
  .xf::before{
    position: absolute;
    content: attr(data-text);
    text-shadow: 0 0 1vmin #53025e,
    0 0 3vmin #731b8d,
    0 0 4vmin #5c1658,
    0 0 5vmin #350032;
    -webkit-filter: blur(0.1vmin) brightness(2);
            filter: blur(0.1vmin) brightness(2); 
    -webkit-animation: flicker 10s linear 0.5s infinite forwards; 
            animation: flicker 10s linear 0.5s infinite forwards;
  }
  
  @-webkit-keyframes flicker{
    0%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    3%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    6%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    9%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    10%{-webkit-filter:  brightness(3);filter:  brightness(3);} 
    20%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    30%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    40%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    50%{-webkit-filter:  brightness(3);filter:  brightness(3);} 
    60%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    90%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    100%{-webkit-filter: brightness(4);filter: brightness(4);} 
  }
  
  @keyframes flicker{
    0%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    3%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    6%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    9%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    10%{-webkit-filter:  brightness(3);filter:  brightness(3);} 
    20%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    30%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    40%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    50%{-webkit-filter:  brightness(3);filter:  brightness(3);} 
    60%{-webkit-filter:  brightness(1);filter:  brightness(1);} 
    90%{-webkit-filter:  brightness(2);filter:  brightness(2);} 
    100%{-webkit-filter: brightness(4);filter: brightness(4);} 
  } 
